<template>
  <div class="goods">
      <options :data="dataOption" v-on:change="optchange" ref="goodsOpt"/>
      <tables :data="dataTable" :list="tableList" v-on:change="tabchange"/>
      <!-- <div class="demonstration-box">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="pageParams.size"
          layout="total, prev, pager, next"
          :total="pageParams.totalCount"
        ></el-pagination>
      </div> -->
      <el-dialog
        title="提示"
        :visible.sync="dialogVisible"
        width="30%">
        <span>{{dialogContent}}</span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="sure">确 定</el-button>
        </span>
      </el-dialog>
  </div>
</template>

<script>
import Options from "views/common/options";
import Tables from "views/common/tables";
import queryString from "querystring";
import page from "data/page";
import { getSchool } from "data/listBody";

const dataOption = {
  leftMenu: [
    {
      name: "校区名称：",
      placeholder: "请选择校区名称",
      module: "schoolId",
      type: "select"
    }
    // { id:'2', name:'班级名称：', placeholder:'请输入班级名称', module:'name'},
    // { id:'3', name:'带班老师：', placeholder:'请输入带班老师', module:'contactPerson'},
    // { id:'4', name:'带班老师手机：', placeholder:'请输入带班老师手机', module:'mobile'},
  ],
  rightMenu: [{ id: "r1", name: "查询" }, { id: "r2", name: "新增" }]
};

const dataTable = {
  table: [
    { label: "校区名称", prop: "schoolName" },
    { label: "班级名称", prop: "name" },
    { label: "带班老师", prop: "teacher" },
    { label: "联系人", prop: "contactPerson" },
    { label: "联系人手机", prop: "mobile" }
  ],
  edit: [{ name: "编辑", id: "t1" }, { name: "删除", id: "t2" }]
};

const tips = ["确认删除该条信息?"];

export default {
  name: "schoolClass",
  mixins: [page],
  data() {
    return {
      dataOption: dataOption,
      dataTable: dataTable,
      dialogVisible: false,
      dialogContent: tips[0],
      tableList: [],
      loading: false,
      currentPage: 1,
      id: ""
    };
  },
  components: {
    Options,
    Tables
  },
  mounted() {
    this.search();
    this.dropInit();
  },
  methods: {
    dropInit() {
      getSchool(res => {
        let { leftMenu } = this.dataOption;
        leftMenu = leftMenu.map(item => {
          if (item.module === "schoolId") item.options = res;
          return item;
        });
        this.dataOption.leftMenu = leftMenu;
      });
    },
    optchange(params) {
      console.log(params.id + "--id被单击了");
      if (params.id === "r1") return this.search();
      if (params.id === "r2")
        return this.$router.push(`/school/classAdd?type=new`);
    },
    tabchange(params) {
      console.log(params.id + "--id被单击了");

      this.id = params.data.id;
      // 编辑
      if (params.id === "t1")
        return this.$router.push(`/school/classAdd?type=edit&id=${this.id}`);
      // 删除
      if (params.id === "t2") return (this.dialogVisible = true);
    },
    sure() {
      this.api.delete(`/class/${this.id}`).then(res => {
          this.loading = false;
          this.search();
          this.$message(res.data);
        })
        .catch(err => {
          console.log(err);
          this.$message(err.data.message);
        });
      this.dialogVisible = false;
    },
    search() {
      //搜索
      const optParams = this.$refs.goodsOpt.getData();
      //   const pageParams = { size: 20, page: this.currentPage - 1 };
      const params = Object.assign({}, optParams);
      this.loading = true;
      this.api.get(`/class?${queryString.stringify(params)}`)
        .then(res => {
          this.loading = false;
          this.tableList = res.data;
        })
        .catch(err => {
          console.log(err);
          this.$message(err.data.message);
        });
    },
    dataInit() {
      //数据初始化
    }
  }
};
</script>
 
<style lang="less" scoped>
</style>
 